.HomeTitle {
    color: white;
    margin-top: 250px;
    font-size: 40pt;
    font-family: "Verdana";
    text-align: center;
    transition: all 1s ease-in-out;
}

.HomeSubTitle {
    font-size: 20pt;
    font-family: "Verdana";
    color: #eee;
    text-align: center;
}

.HomeHeadings {
    /* position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    pointer-events: none; 
